<template>
  <div class="MedIzzinasTable">
    <h4>Autovadītāja medicīniskās izziņas</h4>
    <b-button class="submit-button" @click="$emit('med-new')">Pievienot</b-button>
    <b-form-checkbox
      id="show-active-checkbox"
      v-model="showActive"
      name="show-active-checkbox"
      :value="false"
      :unchecked-value="true"
    >Parādīt nederīgos dokumentus</b-form-checkbox>
    <table
      v-show="info == null && info == undefined"
      class="table table-bordered table-striped table-hover"
    >
      <thead>
        <tr>
          <th>Statuss</th>
          <th>Numurs</th>
          <th>Datums no</th>
          <th>Datums līdz</th>
          <th>Kategorijas</th>
          <th>Ierobežojumi</th>
        </tr>
      </thead>
      <tbody>
        <tr>
          <td colspan="6" style="text-align: center">Nav datu</td>
        </tr>
      </tbody>
    </table>

    <table
      v-show="info !== null && info !== undefined"
      class="table table-bordered table-striped table-hover"
    >
      <thead>
        <tr>
          <th>Statuss</th>
          <th>Numurs</th>
          <th>Datums no</th>
          <th>Datums līdz</th>
          <th>Kategorijas</th>
          <th>Ierobežojumi</th>
        </tr>
      </thead>
      <tbody>
        <tr v-if="filteredIzzinas !== undefined && filteredIzzinas.length == 0">
          <td colspan="6" style="text-align: center">Nav datu</td>
        </tr>
        <tr
          v-for="item in filteredIzzinas"
          :key="item.id"
          :class="{active: item.st == 1, inactive: item.st !== 1}"
          @click="$emit('med-edit', item)"
        >
          <td>
            <div v-show="item.st == 0">Anulēts</div>
            <div v-show="item.st == 2">Nederīgs</div>
            <div v-show="item.st == 1">Aktīvs</div>
          </td>
          <td v-if="item.num">{{ item.num }}</td>
          <td v-if="!item.num">Bez numura</td>
          <td
            v-if="item.sd !== undefined"
          >{{ String(new Date(item.sd).getDate()).padStart(2, '0') }}.{{ String(new Date(item.sd).getMonth()).padStart(2, '0') }}.{{new Date(item.sd).getFullYear()}}</td>
          <td
            v-if="item.bd !== undefined"
          >{{ String(new Date(item.bd).getDate()).padStart(2, '0') }}.{{ String(new Date(item.bd).getMonth()).padStart(2, '0') }}.{{new Date(item.bd).getFullYear()}}</td>
          <td v-if="item.sd == undefined"></td>
          <td v-if="item.bd == undefined"></td>
          <td v-if="item.kk">{{ item.kk.join(', ') }}</td>
          <td v-if="!item.kk"></td>
          <td v-if="item.kier">{{ item.kier.join(', ') }}</td>
          <td v-if="!item.kier"></td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
export default {
  name: "MedIzzinasTable",
  props: ["info"],
  data() {
    return {
      showActive: true,
    };
  },
  computed: {
    filteredIzzinas: function () {
      if (this.info !== null) {
        let result = [];

        if (this.showActive == true) {
          result = this.info.filter((item) => {
            return item.st == 1;
          });
        } else {
          result = this.info.filter((item) => {
            return item.st == 1 || item.st == 2 || item.st == 0;
          });
        }

        return result;
      }
    },
  },
};
</script>

<style scoped>
.submit-button {
  margin-bottom: 10px;
}

.active {
  background: rgba(99, 192, 99, 0.7);
  color: black;
}

.active td {
  background: rgba(99, 192, 99, 0.7);
  color: black;
  cursor: pointer;
}

table {
  max-width: 100%;
  min-width: 50%;
}

.inactive {
  padding-left: 12px;
  padding-right: 12px;
  min-width: 80px;
  color: rgb(87, 87, 87);
}

td a {
  color: black;
  white-space: nowrap;
}
</style>
