<template>
  <div class="LiegumiTable">
    <h4>Transportlīdzekļa vadīšanas aizliegumi</h4>
    <table class="table table-bordered">
      <thead>
        <tr>
          <th>Datums no</th>
          <th>Datums līdz</th>
          <th>Iemesls</th>
        </tr>
      </thead>
      <tr
        v-if="personData.liegumi !== undefined || personData == !null"
        v-for="(item, index) in personData.liegumi"
        :key="index"
      >
        <td v-if="item.sd !== undefined">{{ String(new Date(item.sd).getDate()).padStart(2, '0') }}.{{ String(new Date(item.sd).getMonth()).padStart(2, '0') }}.{{new Date(item.sd).getFullYear()}}</td>
        <td v-if="item.bd !== undefined">{{ String(new Date(item.bd).getDate()).padStart(2, '0') }}.{{ String(new Date(item.bd).getMonth()).padStart(2, '0') }}.{{new Date(item.bd).getFullYear()}}</td>
        <td v-if="item.sd == undefined"></td>
        <td v-if="item.bd == undefined"></td>
        <td>{{ item.pamatojums }}</td>
      </tr>
      <tr v-if="personData.liegumi == undefined || personData == null">
        <td colspan="3" class="inactive">Nav datu</td>
      </tr>
    </table>
  </div>
</template>

<script>
export default {
  name: "LiegumiTable",
  props: ["personData"],
};
</script>
