<template>
  <div class="person-search">
    <b-form @submit="onSubmit">
      <b-form-group
        v-show="!foreigner"
        id="input-group-code"
        label="Personas kods:"
        label-for="input-code-one"
      >
        <b-form-input
          id="input-code-one"
          placeholder="123456-12345"
          v-mask="'######-#####'"
          v-model="person"
          maxlength="12"
          required
          oninvalid="this.setCustomValidity('Lūdzu aizpildiet šo lauku.')"
          onchange="this.setCustomValidity('')"
        ></b-form-input>
        <b-form-checkbox
          id="foreigner-checkbox-one"
          v-model="foreigner"
          name="foreigner-checkbox-one"
          :value="true"
          :unchecked-value="false"
        >Ārzemnieks</b-form-checkbox>
      </b-form-group>

      <b-form-group
        v-show="foreigner"
        id="input-group-code"
        label="Personas koda pirmā daļa (dzimšanas datums):"
        label-for="input-code-two"
      >
        <b-form-input
          id="input-code-two"
          placeholder="123456"
          v-model="person"
          maxlength="6"
          :required="(foreigner == true ? true : false)"
          oninvalid="this.setCustomValidity('Lūdzu aizpildiet šo lauku.')"
          onchange="this.setCustomValidity('')"
        ></b-form-input>
        <b-form-checkbox
          id="foreigner-checkbox-two"
          v-model="foreigner"
          name="foreigner-checkbox-two"
          :value="true"
          :unchecked-value="false"
        >Ārzemnieks</b-form-checkbox>
      </b-form-group>

      <b-form-group
        v-show="foreigner"
        id="input-group-document"
        label="Personu apliecinošā dokumenta numurs:"
        label-for="input-document"
      >
        <b-form-input
          id="input-document"
          :required="(foreigner == true ? true : false)"
          oninvalid="this.setCustomValidity('Lūdzu aizpildiet šo lauku.')"
          onchange="this.setCustomValidity('')"
          placeholder="Dokumenta numurs"
          v-model="document"
          maxlength="24"
        ></b-form-input>
      </b-form-group>
      <b-button type="submit" class="submit-button">Meklēt</b-button>
    </b-form>
  </div>
</template>

<script>
import TheMask from "vue-the-mask";
import toast from "../services/toast";

export default {
  name: "PersKodsSearch",
  components: {
    TheMask,
  },
  data() {
    return {
      person: "",
      document: "",
      foreigner: false,
    };
  },
  methods: {
    persCode() {
      return this.person.replace("-", "");
    },
    showError(error) {
      this.$emit("error", error);
    },
    onSubmit() {
      event.preventDefault();

      axios
        .post("/api/personas-dati", {
          sessionId: sessionStorage.getItem("sessionId"),
          pers_kods: this.persCode(),
        })
        .then((response) => {
          if (response.data.Errors !== undefined) {
            toast.error(response.data.Errors[0].ClientMessage);
          } else {
            this.$emit("personFound", response.data.rowset[0]);
            this.$emit("persKods", this.person);
          }
        })
        .catch((error) => console.log(error));
    },
  },
};
</script>

<style scoped>
.submit-button {
  width: 85px;
  margin-top: 5px;
}
</style>
