<template>
  <div class="person-search">
    <div class="row">
      <div class="col-md-6 offset-md-3" v-if="(!searched && !form)">
        <PersKodsSearch
          v-on:personFound="personFound"
          v-on:persKods="setPersKods"
          v-on:error="onError"
        />
      </div>
    </div>
    <div v-show="searched" class="tables col-md-8 offset-2">
      <b-button class="submit-button" @click="back">Atpakaļ</b-button>
      <div class="row data" v-if="personData !== null">
        <div :class="{ 'offset-3': form }" class="col-md-6">
          <h4>Personas informācija</h4>
          <PersInfoTable :personData="personData" />
        </div>
      </div>
      <div class="row data" v-if="personData !== null && form !== true">
        <div class="col-md-6">
          <h4>Policijas lēmumi par norīkošanu uz pirmstermiņa veselības pārbaudēm</h4>
          <PolicijasPirmsterminaTable :personData="personData" />
        </div>
      </div>
      <MedIzzinasForm
        v-if="medForm"
        :izzinasData="izzinasData"
        :personData="personData"
        :persKods="persKods"
        v-on:med-delete="medDelete"
        v-on:doneForm="doneForm"
      />
      <PretindikacijasForm
        v-if="pretindikacijasForm"
        :izzinasData="izzinasData"
        :personData="personData"
        :persKods="persKods"
        v-on:pret-delete="pretDelete"
        v-on:doneForm="doneForm"
      />
      <PirmsterminaForm
        v-if="pirmsterminaForm"
        :izzinasData="izzinasData"
        :personData="personData"
        :persKods="persKods"
        v-on:pirms-delete="pirmsDelete"
        v-on:doneForm="doneForm"
      />
      <div class="row data" v-if="!form">
        <div class="col-md-12">
          <MedIzzinasTable :info="medIzzinas" v-on:med-new="medNew" v-on:med-edit="medEdit" />
        </div>
      </div>
      <div class="row data" v-if="!form">
        <div class="col-md-12">
          <PretindikacijasTable
            :info="pretindikacijas"
            v-on:pret-new="pretNew"
            v-on:pret-edit="pretEdit"
          />
        </div>
      </div>
      <div class="row data" v-if="!form">
        <div class="col-md-12">
          <PirmsterminaTable
            :info="pirmstermina"
            v-on:pirms-new="pirmsNew"
            v-on:pirms-edit="pirmsEdit"
          />
        </div>
      </div>
      <div class="row data" v-if="personData !== null && form !== true">
        <div class="col-md-12">
          <LiegumiTable :personData="personData" />
        </div>
      </div>
    </div>
    <br />
    <!--<div class="col-md-6 offset-3">
      <b-button
        variant="dark"
        block
        @click="imageEdit == true ? imageEdit = false : imageEdit = true"
      >ImageEdit</b-button>
    </div>
    <ImageEdit v-if="imageEdit" />-->
  </div>
</template>

<script>
import MedIzzinasTable from "./MedIzzinasTable";
import PretindikacijasTable from "./PretindikacijasTable";
import PirmsterminaTable from "./PirmsterminaTable";
import PersInfoTable from "./PersInfoTable";
import PolicijasPirmsterminaTable from "./PolicijasPirmsterminaTable";
import LiegumiTable from "./LiegumiTable";
import PersKodsSearch from "./PersKodsSearch";
import MedIzzinasForm from "./MedIzzinasForm";
import PretindikacijasForm from "./PretindikacijasForm";
import PirmsterminaForm from "./PirmsterminaForm";
import ImageEdit from "./ImageEdit";

import toast from "../services/toast";
import "izitoast/dist/css/iziToast.min.css";
import iZiToast from "izitoast";

export default {
  name: "PersonSearch",
  components: {
    MedIzzinasTable,
    PretindikacijasTable,
    PirmsterminaTable,
    PersInfoTable,
    PolicijasPirmsterminaTable,
    LiegumiTable,
    PersKodsSearch,
    MedIzzinasForm,
    PretindikacijasForm,
    PirmsterminaForm,
    ImageEdit,
  },
  data() {
    return {
      searched: false,
      personData: null,
      medIzzinas: null,
      pretindikacijas: null,
      pirmstermina: null,
      form: false,
      medForm: false,
      pretindikacijasForm: false,
      pirmsterminaForm: false,
      persKods: null,
      izzinasData: null,
      imageEdit: false,
      arsts: null,
    };
  },
  methods: {
    back() {
      if (this.form == false) {
        this.searched = false;
      } else if (this.form == true) {
        this.form = false;
        this.medForm = false;
        this.pretindikacijasForm = false;
        this.pirmsterminaForm = false;
      }
    },
    doneForm() {
      this.form = false;
      this.medForm = false;
      this.pretindikacijasForm = false;
      this.pirmsterminaForm = false;
      this.izzinasData = null;
      this.getInfo();
    },
    medNew() {
      this.form = true;
      this.medForm = true;
      this.izzinasData = null;
    },
    medEdit(item) {
      this.izzinasData = null;
      if (item.var_labot == true && item.st == 1) {
        this.form = true;
        this.medForm = true;
        this.izzinasData = item;
      } else {
        //toast.error("Šo izziņu nevar labot.");
        console.log("Nevar labot");
      }
    },
    medDelete(item) {
      var vue = this;
      iZiToast.question({
        timeout: 20000,
        close: false,
        overlay: true,
        displayMode: "once",
        id: "question",
        zindex: 999,
        title: "Anulēt",
        message: "Vai tiešām vēlaties anulēt izziņu?",
        position: "center",
        buttons: [
          [
            "<button><b>Jā</b></button>",
            function (instance, toast) {
              instance.hide({ transitionOut: "fadeOut" }, toast, "button");
              vue.medDeleteRequest(item);
            },
          ],
          [
            "<button>Nē</button>",
            function (instance, toast) {
              instance.hide({ transitionOut: "fadeOut" }, toast, "button");
            },
          ],
        ],
      });
    },
    medDeleteRequest(item) {
      axios
        .post("api/med-izzina", {
          sessionId: sessionStorage.getItem("sessionId"),
          id: item.id,
          gprs_id: this.personData.id,
          gprs_pk: this.persKods,
          gprs_vards: this.personData.vards,
          gprs_uzvards: this.personData.uzvards,
          kaim_kods: "VMI_ANUL",
        })
        .then((response) => {
          if (response.data.Errors !== undefined) {
            toast.error(response.data.Errors[0].ClientMessage);
          } else {
            toast.success("Izziņa anulēta veiksmīgi.");
            this.doneForm();
          }
        })
        .catch((error) => console.log(error));
    },
    pretNew() {
      this.form = true;
      this.pretindikacijasForm = true;
      this.izzinasData = null;
    },
    pretEdit(item) {
      this.izzinasData = null;
      if (item.var_labot == true && item.st == 1) {
        this.form = true;
        this.pretindikacijasForm = true;
        this.izzinasData = item;
      } else {
        //toast.error("Šo izziņu nevar labot.");
        console.log("Nevar labot");
      }
    },
    pretDelete(izzinasData) {
      var vue = this;
      iZiToast.question({
        timeout: 20000,
        close: false,
        overlay: true,
        displayMode: "once",
        id: "question",
        zindex: 999,
        title: "Anulēt",
        message: "Vai tiešām vēlaties anulēt izziņu?",
        position: "center",
        buttons: [
          [
            "<button><b>Jā</b></button>",
            function (instance, toast) {
              instance.hide({ transitionOut: "fadeOut" }, toast, "button");
              vue.pretDeleteRequest(izzinasData);
            },
          ],
          [
            "<button>Nē</button>",
            function (instance, toast) {
              instance.hide({ transitionOut: "fadeOut" }, toast, "button");
            },
          ],
        ],
      });
    },
    pretDeleteRequest(izzinasData) {
      axios
        .post("api/pretindikacijas-izzina", {
          sessionId: sessionStorage.getItem("sessionId"),
          id: izzinasData.izzinasData.id,
          gprs_id: this.personData.id,
          //snpv_id: izzinasData.izzinasData.id,
          sd: izzinasData.izzinasData.sd,
          ssa_id: izzinasData.arsts,
          kaim_kods: "SPVPA_ANUL",
          num: izzinasData.num,
        })
        .then((response) => {
          if (response.data.Errors !== undefined) {
            toast.error(response.data.Errors[0].ClientMessage);
            console.log(response);
          } else {
            toast.success("Izziņa anulēta veiksmīgi.");
            this.doneForm();
          }
        })
        .catch((error) => console.log(error));
    },
    pirmsNew() {
      this.form = true;
      this.pirmsterminaForm = true;
      this.izzinasData = null;
    },
    pirmsEdit(item) {
      this.izzinasData = null;
      if (item.var_labot == true && item.st == 1) {
        this.form = true;
        this.pirmsterminaForm = true;
        this.izzinasData = item;
      } else {
        //toast.error("Šo izziņu nevar labot.");
        console.log("Nevar labot");
      }
    },
    pirmsDelete(izzinasData) {
      var vue = this;
      iZiToast.question({
        timeout: 20000,
        close: false,
        overlay: true,
        displayMode: "once",
        id: "question",
        zindex: 999,
        title: "Anulēt",
        message: "Vai tiešām vēlaties anulēt izziņu?",
        position: "center",
        buttons: [
          [
            "<button><b>Jā</b></button>",
            function (instance, toast) {
              instance.hide({ transitionOut: "fadeOut" }, toast, "button");
              vue.pirmsDeleteRequest(izzinasData);
            },
          ],
          [
            "<button>Nē</button>",
            function (instance, toast) {
              instance.hide({ transitionOut: "fadeOut" }, toast, "button");
            },
          ],
        ],
      });
    },
    pirmsDeleteRequest(izzinasData) {
      axios
        .post("api/pirmstermina-izzina", {
          sessionId: sessionStorage.getItem("sessionId"),
          id: izzinasData.id,
          gprs_id: this.personData.id,
          sd: izzinasData.sd,
          kaim_kods: "SPVPMI_ANUL",
        })
        .then((response) => {
          if (response.data.Errors !== undefined) {
            toast.error(response.data.Errors[0].ClientMessage);
            console.log(response);
          } else {
            toast.success("Izziņa anulēta veiksmīgi.");
            this.doneForm();
          }
        })
        .catch((error) => console.log(error));
    },
    onError(event) {
      this.searched = false;
      toast.error(event["ClientMessage"]);
    },
    setPersKods(persKods) {
      this.persKods = persKods;
    },
    personFound(personData) {
      this.showError = false;
      this.errorMessage = null;

      this.searched = true;
      this.form = false;
      this.medForm = false;

      this.personData = personData;

      this.getInfo();
    },
    getInfo() {
      axios
        .post("/api/med-izzinas-saraksts", {
          sessionId: sessionStorage.getItem("sessionId"),
          pers_id: this.personData.id,
        })
        .then((response) => {
          if (response.data.Errors !== undefined) {
            toast.error(response.data.Errors[0].ClientMessage);
          }
          this.medIzzinas = response.data.rowset;
        })
        .catch((error) => console.log(error));

      axios
        .post("/api/pretindikacijas-saraksts", {
          sessionId: sessionStorage.getItem("sessionId"),
          pers_id: this.personData.id,
        })
        .then((response) => {
          if (response.data.Errors !== undefined) {
            toast.error(response.data.Errors[0].ClientMessage);
          }
          this.pretindikacijas = response.data.rowset;
        })
        .catch((error) => console.log(error));

      axios
        .post("/api/pirmstermina-saraksts", {
          sessionId: sessionStorage.getItem("sessionId"),
          pers_id: this.personData.id,
        })
        .then((response) => {
          if (response.data.Errors !== undefined) {
            toast.error(response.data.Errors[0].ClientMessage);
          }
          this.pirmstermina = response.data.rowset;
        })
        .catch((error) => console.log(error));
    },
  },
};
</script>

<style scoped>
.submit-button {
  width: 85px;
  margin-top: 5px;
}

.tables .data {
  margin-top: 25px;
}

.vdr {
  border: none;
}
</style>
