<template>
  <div class="person-search">
    <div class="row" style="margin-top: 25px; margin-bottom: 25px;">
      <div class="col-md-6 offset-3">
        <div id="my-node" style="width: 307px; height: 388px;">
          <vue-draggable-resizable
            v-if="imageDone"
            :w="100"
            :h="100"
            @dragging="onDrag"
            @resizing="onResize"
            :parent="true"
            class="backdrop-blur"
            style="z-index: 998;"
            :style="cssProps"
          ></vue-draggable-resizable>
          <croppa
            :width="307"
            :height="388"
            placeholder="Izvēlieties attēlu"
            placeholder-color="#000"
            :placeholder-font-size="16"
            canvas-color="transparent"
            :show-remove-button="!imageDone"
            remove-button-color="#006225"
            :prevent-white-space="true"
            :zoom-speed="10"
            v-model="image"
            :disable-click-to-choose="imageDone"
            :disable-drag-and-drop="imageDone"
            :disable-drag-to-move="imageDone"
            :disable-scroll-to-zoom="imageDone"
            :disable-pinch-to-zoom="imageDone"
            :disable-rotation="imageDone"
            @loading-end="loaded = true"
            @image-remove="handleImageRemove"
          ></croppa>
        </div>
      </div>
    </div>
    <div class="row">
      <div class="col-md-3 offset-3">
        <div class="row">
          <b-button class="submit-button col-md-5" @click="image.rotate()">Pagriezt pa kreisi</b-button>
          <b-button
            class="submit-button col-md-5 offset-1"
            @click="image.rotate(-1)"
          >Pagriezt pa labi</b-button>
        </div>
        <br />
        <div class="row">
          <b-button
            class="col-md-5"
            v-if="loaded"
            @click="imageDone == true ? imageDone = false : imageDone = true"
          >{{ imageDone == true ? "Atsākt" : "Pabeigt" }}</b-button>
          <b-button
            v-if="imageDone"
            class="col-md-5 offset-1"
            @click="download('image/jpeg', 1.0)"
          >Lejupielādēt</b-button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import toast from "../services/toast";
import "izitoast/dist/css/iziToast.min.css";
import iZiToast from "izitoast";

import VueDraggableResizable from "vue-draggable-resizable";
import "vue-draggable-resizable/dist/VueDraggableResizable.css";

import domtoimage from "dom-to-image";
import { saveAs } from "file-saver";

export default {
  name: "PersonSearch",
  components: {
    VueDraggableResizable,
  },
  data() {
    return {
      image: {},
      dataUrl: "",
      imageDone: false,
      width: 100,
      height: 100,
      x: 0,
      y: 0,
      loaded: false,
    };
  },
  methods: {
    handleImageRemove() {
      this.loaded = false;
    },
    onResize: function (x, y, width, height) {
      this.x = x;
      this.y = y;
      this.width = width;
      this.height = height;
    },
    onDrag: function (x, y) {
      this.x = x;
      this.y = y;
    },
    toFormData(obj) {
      var form_data = new FormData();
      for (var key in obj) {
        form_data.append(key, obj[key]);
      }
      return form_data;
    },
    download(type, compressionRate) {
      var vue = this;
      domtoimage
        .toPng(document.getElementById("my-node"))
        .then(async function (Png) {
          //window.saveAs(blob, "my-node.png");

          var formData = vue.toFormData(Png);

          axios
            .post("/api/blob", {
              data: Png,
            })
            .then((response) => {
              console.log(response.data);
              window.saveAs(Png, "my-node.png");
            })
            .catch((error) => console.log(error));
        });

      /*console.log(document.getElementById("my-node"));
      this.image.generateBlob(
        (blob) => {
          //te es varu padot blobu uz api
          var url = URL.createObjectURL(blob);
          var a = document.createElement("a");
          a.download = "filename";
          a.href = url;
          a.click();
          URL.revokeObjectURL(url);
        },
        type,
        compressionRate
      );*/
    },
  },
  computed: {
    cssProps() {
      return {
        "--height": this.height + "px",
        "--width": this.width + "px",
        "--bg-hover-color": this.bgHoverColor,
      };
    },
  },
};
</script>

<style scoped>
:root {
  --height: 100px;
  --width: 100px;
}

.backdrop-blur {
  /*background: url("./image.png");*/
  background: url("./image.jpg");
  background-repeat: no-repeat;
  background-size: var(--width) var(--height);
  /*image-rendering: pixelated;*/
  /*box-shadow: inset 0 0 30px 15px #212121;*/
  /*filter: blur(20px);*/
}
</style>
