<template>
  <div class="container">
    <b-form @submit="onSubmit">
      <div class="row">
        <div class="col-md-6 offset-3">
          <div class="row">
            <label for="numurs">Atzinuma numurs:</label>
            <b-form-input
              v-model="numurs"
              id="numurs"
              v-mask="'######'"
              placeholder="Atzinuma numurs"
            ></b-form-input>
          </div>
          <div class="row">
            <label for="arsti">Izsniedza:</label>
            <b-form-select v-model="arsts" id="arsti" :options="arsti"></b-form-select>
          </div>
        </div>
      </div>

      <div class="row datums">
        <div class="col-md-9 offset-3 sakuma-datums">
          Izvēlieties sākuma datumu:
          <datepicker
            :value="startDate"
            name="startDate"
            :language="lv"
            :monday-first="true"
            format="dd MMMM yyyy"
            :required="true"
            :disabled-dates="{to: allowedDateTo, from: allowedDateFrom}"
            v-on:selected="setStartDate"
          ></datepicker>
        </div>
      </div>
      <div class="row">
        <div class="col-md-9 offset-3 pogas">
          <b-button
            class="submit-button"
            v-on:click="[yearsToAdd = yearsToAdd + 1, addYears]"
          >+1 gads</b-button>
          <b-button class="submit-button" @click="[yearsToAdd = yearsToAdd + 2, addYears]">+2 gads</b-button>
          <b-button class="submit-button" @click="[yearsToAdd = yearsToAdd + 3, addYears]">+3 gadi</b-button>
          <br />
          <b-button class="submit-button" @click="[yearsToAdd = yearsToAdd + 5, addYears]">+5 gadi</b-button>
          <b-button class="submit-button" @click="[yearsToAdd = yearsToAdd + 10, addYears]">+10 gadi</b-button>
          <b-button
            class="submit-button"
            @click="[yearsToAdd = 0, addYears, startDate = new Date(), endDate = new Date()]"
          >Šodiena</b-button>
        </div>
      </div>
      <div class="row">
        <div class="col-md-9 offset-3 beigu-datums">
          Izvēlieties beigu datumu:
          <datepicker
            :value="endDate"
            name="endDate"
            :language="lv"
            :monday-first="true"
            format="dd MMMM yyyy"
            :required="true"
            :disabled-dates="{to: allowedDateTo, from: allowedDateFrom}"
            v-on:selected="setEndDate"
            v-on:input="endDateChanged"
          ></datepicker>
        </div>
      </div>

      <div class="row">
        <div class="col-md-6 offset-3">
          <b-button v-if="edit == false" type="submit" class="submit-button">Pievienot</b-button>
          <b-button v-if="edit == true" type="submit" class="submit-button">Labot</b-button>
          <b-button
            v-if="edit == true"
            variant="danger"
            style="margin-top: 5px"
            @click="$emit('pret-delete', {'izzinasData': izzinasData, 'arsts': arsts})"
          >Dzēst</b-button>
        </div>
      </div>
    </b-form>
  </div>
</template>

<script>
import TheMask from "vue-the-mask";

import Datepicker from "vuejs-datepicker";
import { en, lv } from "vuejs-datepicker/dist/locale";

import Treeselect from "@riophae/vue-treeselect";
import "@riophae/vue-treeselect/dist/vue-treeselect.css";

import toast from "../services/toast";

export default {
  name: "PretindikacijasForm",
  components: {
    TheMask,
    Datepicker,
    Treeselect,
  },
  data() {
    return {
      edit: false,
      arsti: [],
      arsts: null,
      dateChanged: false,
      numurs: null,
      startDate: new Date(),
      endDate: new Date(),
      lv: lv,
      yearsToAdd: 0,
      allowedDateTo: new Date(Date.now() - 7 * 24 * 60 * 60 * 1000),
      allowedDateFrom: new Date(
        new Date().setFullYear(
          new Date().getFullYear() + 20,
          new Date().getMonth() + 1,
          0
        )
      ),
    };
  },
  props: ["personData", "persKods", "izzinasData"],
  methods: {
    endDateChanged() {
      this.dateChanged = true;
    },
    onSubmit() {
      event.preventDefault();

      if (this.edit == false) {
        axios
          .post("api/pretindikacijas-izzina", {
            sessionId: sessionStorage.getItem("sessionId"),
            gprs_id: this.personData.id,
            kaim_kods: "SPVPA_REG",
            num: this.numurs,
            sd: this.startDate.toISOString().split("T")[0],
            der_datums: this.endDate.toISOString().split("T")[0],
            ssa_id: this.arsts,
          })
          .then((response) => {
            if (response.data.Errors !== undefined) {
              toast.error(response.data.Errors[0].ClientMessage);
            } else {
              toast.success("Izziņa izveidota veiksmīgi.");
              this.$emit("doneForm");
            }
          })
          .catch((error) => console.log(error));
      } else {
        axios
          .post("api/pretindikacijas-izzina", {
            sessionId: sessionStorage.getItem("sessionId"),
            gprs_id: this.personData.id,
            id: this.izzinasData.id,
            kaim_kods: "SPVPA_LAB",
            num: this.numurs,
            snpv_id: this.izzinasData.snpv_id,
            sd: this.startDate.toISOString().split("T")[0],
            der_datums: this.endDate.toISOString().split("T")[0],
            ssa_id: this.arsts,
          })
          .then((response) => {
            if (response.data.Errors !== undefined) {
              toast.error(response.data.Errors[0].ClientMessage);
            } else {
              toast.success("Izziņa labota veiksmīgi.");
              this.$emit("doneForm");
            }
          })
          .catch((error) => console.log(error));
      }
    },
    setStartDate(event) {
      this.startDate = event;
    },
    setEndDate(event) {
      this.endDate = event;
    },
    dateToString() {
      //console.log(this.startDate.toISOString().split("T")[0]);
      //console.log(this.startDate.toISOString().split("T")[0]);

      var startMm = this.startDate.getMonth() + 1;
      var startDd = this.startDate.getDate();

      var startDateString = [
        (startDd > 9 ? "" : "0") + startDd,
        (startMm > 9 ? "" : "0") + startMm,
        this.startDate.getFullYear(),
      ].join(".");

      var endMm = this.endDate.getMonth() + 1;
      var endDd = this.endDate.getDate();

      var endDateString = [
        (startDd > 9 ? "" : "0") + endDd,
        (endMm > 9 ? "" : "0") + endMm,
        this.endDate.getFullYear(),
      ].join(".");

      /*console.log(
        "Start date: " + startDateString + "\n",
        "End date: " + endDateString
      );*/
    },
  },
  computed: {
    addYears() {
      this.dateChanged = true;
      if (this.yearsToAdd <= 20) {
        this.endDate = new Date(
          this.endDate.setFullYear(new Date().getFullYear() + this.yearsToAdd)
        );
      } else {
        this.endDate = new Date(
          this.endDate.setFullYear(new Date().getFullYear() + 20)
        );
      }
      this.dateToString();
    },
  },
  mounted() {
    axios
      .post("api/arstu-saraksts", {
        sessionId: sessionStorage.getItem("sessionId"),
      })
      .then((response) => {
        if (response.data.Errors !== undefined) {
          toast.error(response.data.Errors[0].ClientMessage);
        } else {
          var arstuSaraksts = response.data.rowset;

          arstuSaraksts.forEach((element) => {
            this.arsti.push({
              value: element.id,
              text: element.vards + " " + element.uzvards,
            });
          });

          if (this.izzinasData !== null && this.izzinasData !== undefined) {
            this.edit = true;
            this.startDate = new Date(this.izzinasData.sd);
            this.endDate = new Date(this.izzinasData.der_datums);
            this.numurs = this.izzinasData.num;
            var arsts2 = (
              this.izzinasData.ssa_vards +
              " " +
              this.izzinasData.ssa_uzvards
            ).toUpperCase();
            this.arsts = this.arsti.find(
              (element) => element.text == arsts2
            ).value;
          }
        }
      })
      .catch((error) => console.log(error));
  },
};
</script>

<style scoped>
@import url("https://unpkg.com/vue-multiselect@2.1.0/dist/vue-multiselect.min.css");

.container {
  text-align: left;
}

.submit-button {
  margin-top: 20px;
}

.datums {
  margin-top: 10px;
}

.pogas {
  margin-top: 10px;
  margin-bottom: 10px;
}

.pogas .submit-button {
  width: 85px;
  margin-top: 5px;
}

.ierobezojumi {
  margin-top: 20px;
  margin-bottom: 10px;
}
</style>
