<template>
  <div class="PretindikacijasTable">
    <h4>Atzinumi par medicīniskām pretindikācijām</h4>
    <b-button class="submit-button" @click="$emit('pret-new')">Pievienot</b-button>
    <table
      v-show="info == null && info == undefined"
      class="table table-bordered table-striped table-hover"
    >
      <thead>
        <tr>
          <th>Statuss</th>
          <th>Numurs</th>
          <th>Lēmuma numurs</th>
          <th>Datums no</th>
          <th>Datums līdz</th>
          <th>Iestāde</th>
          <th>Sertificētais ārsts</th>
        </tr>
      </thead>
      <tbody>
        <tr>
          <td colspan="7" style="text-align: center">Nav datu</td>
        </tr>
      </tbody>
    </table>

    <table
      v-show="info !== null && info !== undefined"
      class="table table-bordered table-striped table-hover"
    >
      <thead>
        <tr>
          <th>Statuss</th>
          <th>Numurs</th>
          <th>Lēmuma numurs</th>
          <th>Datums no</th>
          <th>Datums līdz</th>
          <th>Iestāde</th>
          <th>Sertificētais ārsts</th>
        </tr>
      </thead>
      <tbody>
        <tr
          v-for="item in info"
          :key="item.id"
          :class="{active: item.st == 1, inactive: item.st !== 1}"
          @click="$emit('pret-edit', item)"
        >
          <td>
            <div v-show="item.st == 0">Anulēts</div>
            <div v-show="item.st == 2">Nederīgs</div>
            <div v-show="item.st == 1">Aktīvs</div>
          </td>
          <td v-if="item.num">{{ item.num }}</td>
          <td v-if="!item.num">Bez numura</td>
          <td v-if="item.snpv_num">{{ item.snpv_num }}</td>
          <td v-if="!item.snpv_num">Bez numura</td>
          <td
            v-if="item.sd !== undefined"
          >{{ String(new Date(item.sd).getDate()).padStart(2, '0') }}.{{ String(new Date(item.sd).getMonth()).padStart(2, '0') }}.{{new Date(item.sd).getFullYear()}}</td>
          <td
            v-if="item.der_datums !== undefined"
          >{{ String(new Date(item.der_datums).getDate()).padStart(2, '0') }}.{{ String(new Date(item.der_datums).getMonth()).padStart(2, '0') }}.{{new Date(item.der_datums).getFullYear()}}</td>
          <td v-if="item.sd == undefined"></td>
          <td v-if="item.der_datums == undefined"></td>
          <td>{{ item.kiest_nos }}</td>
          <td>{{ item.ssa_vards }} {{ item.ssa_uzvards }}</td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
export default {
  name: "PretindikacijasTable",
  props: ["info"],
};
</script>

<style scoped>
.submit-button {
  margin-bottom: 10px;
}

.active {
  background: rgba(99, 192, 99, 0.7);
  color: black;
}

.active td {
  background: rgba(99, 192, 99, 0.7);
  color: black;
  cursor: pointer;
}

table {
  max-width: 100%;
  min-width: 50%;
}

.inactive {
  padding-left: 12px;
  padding-right: 12px;
  min-width: 80px;
  color: rgb(87, 87, 87);
}

td a {
  color: black;
  white-space: nowrap;
}
</style>
