<template>
  <div class="PolicijasPirmsterminaTable">
    <table class="table table-bordered">
      <thead>
        <tr>
          <th>Lēmuma numurs</th>
          <th>Datums</th>
          <th>Iestāde</th>
        </tr>
      </thead>
      <tr v-if="personData.pvp == true">
        <td>{{ personData.snpvp[0].num }}</td>
        <td v-if="personData.snpvp[0].datums !== undefined">{{ String(new Date(personData.snpvp[0].datums).getDate()).padStart(2, '0') }}.{{ String(new Date(personData.snpvp[0].datums).getMonth()).padStart(2, '0') }}.{{new Date(personData.snpvp[0].datums).getFullYear()}}</td>
        <td v-if="personData.snpvp[0].datums == undefined"></td>
        <td>{{ personData.snpvp[0].kpiest_nos }}</td>
      </tr>
      <tr v-if="personData.pvp == false">
        <td colspan="3" style="text-align: center">Nav datu</td>
      </tr>
    </table>
  </div>
</template>

<script>
export default {
  name: "PolicijasPirmsterminaTable",
  props: ["personData"],
};
</script>
