<template>
  <div class="container">
    <b-form @submit="onSubmit">
      <div class="row">
        <div class="col-md-6 offset-3">
          <div class="row">
            <h4
              v-if="izzinasData !== null && izzinasData.num !== null && izzinasData.num !== undefined"
            >Izziņas numurs: {{ izzinasData.num }}</h4>
          </div>
          <b-form-group label="Atļautās kategorijas:">
            <b-form-checkbox-group
              v-if="atlautieKk !== null"
              id="checkbox-group-1"
              v-model="kategorijas"
              name="kategorijas"
            >
              <div class="row">
                <div class="col-md-1 offset-1">
                  <b-form-checkbox :value="atlautieKk[0]">{{ atlautieKk[0] }}</b-form-checkbox>
                </div>
                <div class="col-md-1 offset-1">
                  <b-form-checkbox :value="atlautieKk[1]">{{ atlautieKk[1] }}</b-form-checkbox>
                </div>
                <div class="col-md-1 offset-1">
                  <b-form-checkbox :value="atlautieKk[2]">{{ atlautieKk[2] }}</b-form-checkbox>
                </div>
                <div class="col-md-1 offset-1">
                  <b-form-checkbox :value="atlautieKk[3]">{{ atlautieKk[3] }}</b-form-checkbox>
                </div>
              </div>
              <div class="row">
                <div class="col-md-1 offset-1">
                  <b-form-checkbox :value="atlautieKk[4]">{{ atlautieKk[4] }}</b-form-checkbox>
                </div>
                <div class="col-md-1 offset-1">
                  <b-form-checkbox :value="atlautieKk[5]">{{ atlautieKk[5] }}</b-form-checkbox>
                </div>
                <div class="col-md-1 offset-1">
                  <b-form-checkbox :value="atlautieKk[6]">{{ atlautieKk[6] }}</b-form-checkbox>
                </div>
                <div class="col-md-1 offset-1">
                  <b-form-checkbox :value="atlautieKk[7]">{{ atlautieKk[7] }}</b-form-checkbox>
                </div>
              </div>
              <div class="row">
                <div class="col-md-1 offset-1">
                  <b-form-checkbox :value="atlautieKk[8]">{{ atlautieKk[8] }}</b-form-checkbox>
                </div>
                <div class="col-md-1 offset-1">
                  <b-form-checkbox :value="atlautieKk[9]">{{ atlautieKk[9] }}</b-form-checkbox>
                </div>
                <div class="col-md-1 offset-1">
                  <b-form-checkbox :value="atlautieKk[10]">{{ atlautieKk[10] }}</b-form-checkbox>
                </div>
                <div class="col-md-1 offset-1">
                  <b-form-checkbox :value="atlautieKk[11]">{{ atlautieKk[11] }}</b-form-checkbox>
                </div>
              </div>
              <div class="row">
                <div class="col-md-1 offset-1">
                  <b-form-checkbox :value="atlautieKk[12]">{{ atlautieKk[12] }}</b-form-checkbox>
                </div>
                <div class="col-md-1 offset-1">
                  <b-form-checkbox :value="atlautieKk[13]">{{ atlautieKk[13] }}</b-form-checkbox>
                </div>
                <div class="col-md-1 offset-1">
                  <b-form-checkbox :value="atlautieKk[14]">{{ atlautieKk[14] }}</b-form-checkbox>
                </div>
                <div class="col-md-1 offset-1">
                  <b-form-checkbox :value="atlautieKk[15]">{{ atlautieKk[15] }}</b-form-checkbox>
                </div>
              </div>
              <div class="row">
                <div class="col-md-1 offset-1">
                  <b-form-checkbox :value="atlautieKk[16]">{{ atlautieKk[16] }}</b-form-checkbox>
                </div>
                <div class="col-md-1 offset-1">
                  <b-form-checkbox :value="atlautieKk[17]">{{ atlautieKk[17] }}</b-form-checkbox>
                </div>
              </div>
            </b-form-checkbox-group>
          </b-form-group>
        </div>
      </div>

      <div class="row ierobezojumi">
        <div class="col-md-6 offset-3">
          <treeselect
            :multiple="true"
            :clearable="true"
            :searchable="true"
            :options="model"
            v-model="ierobezojumi"
            v-on:select="onGroupClick"
            :allowClearingDisabled="true"
            :allowSelectingDisabledDescendants="true"
            v-on:deselect="onDeselect"
            placeholder="Izvēlieties ierobežojumus, ja tādi nepieciešami"
          />
        </div>
      </div>

      <div class="row datums">
        <div class="col-md-9 offset-3 sakuma-datums">
          Izvēlieties sākuma datumu:
          <datepicker
            :value="startDate"
            name="startDate"
            :language="lv"
            :monday-first="true"
            format="dd MMMM yyyy"
            :required="true"
            :disabled-dates="{to: allowedDateTo, from: allowedDateFrom}"
            v-on:selected="setStartDate"
          ></datepicker>
        </div>
      </div>
      <div class="row">
        <div class="col-md-9 offset-3 pogas">
          <b-button
            class="submit-button"
            v-on:click="[yearsToAdd = yearsToAdd + 1, addYears]"
          >+1 gads</b-button>
          <b-button class="submit-button" @click="[yearsToAdd = yearsToAdd + 2, addYears]">+2 gads</b-button>
          <b-button class="submit-button" @click="[yearsToAdd = yearsToAdd + 3, addYears]">+3 gadi</b-button>
          <br />
          <b-button class="submit-button" @click="[yearsToAdd = yearsToAdd + 5, addYears]">+5 gadi</b-button>
          <b-button class="submit-button" @click="[yearsToAdd = yearsToAdd + 10, addYears]">+10 gadi</b-button>
          <b-button
            class="submit-button"
            @click="[yearsToAdd = 0, addYears, startDate = new Date(), endDate = new Date()]"
          >Šodiena</b-button>
        </div>
      </div>
      <div class="row">
        <div class="col-md-9 offset-3 beigu-datums">
          Izvēlieties beigu datumu:
          <datepicker
            :value="endDate"
            name="endDate"
            :language="lv"
            :monday-first="true"
            format="dd MMMM yyyy"
            :required="true"
            :disabled-dates="{to: allowedDateTo, from: allowedDateFrom}"
            v-on:selected="setEndDate"
            v-on:input="endDateChanged"
          ></datepicker>
        </div>
      </div>

      <div class="row">
        <div class="col-md-6 offset-3">
          <b-button v-if="edit == false" type="submit" class="submit-button">Pievienot</b-button>
          <b-button v-if="edit == true" type="submit" class="submit-button">Labot</b-button>
          <b-button
            v-if="edit == true"
            variant="danger"
            style="margin-top: 5px"
            @click="$emit('med-delete', izzinasData)"
          >Dzēst</b-button>
        </div>
      </div>
    </b-form>
  </div>
</template>

<script>
import Datepicker from "vuejs-datepicker";
import { en, lv } from "vuejs-datepicker/dist/locale";

import Treeselect from "@riophae/vue-treeselect";
import "@riophae/vue-treeselect/dist/vue-treeselect.css";

import toast from "../services/toast";

export default {
  name: "Questions",
  components: {
    Datepicker,
    Treeselect,
  },
  data() {
    return {
      atlautieKk: null,
      edit: false,
      dateChanged: false,
      ierobezojumi: [],
      kategorijas: [],
      model: [],
      startDate: new Date(),
      endDate: new Date(),
      lv: lv,
      yearsToAdd: 0,
      allowedDateTo: new Date(Date.now() - 7 * 24 * 60 * 60 * 1000),
      allowedDateFrom: new Date(
        new Date().setFullYear(
          new Date().getFullYear() + 20,
          new Date().getMonth() + 1,
          0
        )
      ),
    };
  },
  props: ["personData", "persKods", "izzinasData"],
  methods: {
    endDateChanged() {
      this.dateChanged = true;
    },
    onDeselect(node) {
      if (node.children !== undefined || node.children !== null) {
        this.model.forEach((element) => {
          if (element.children !== undefined && element.label == node.label) {
            element.children.forEach((child) => {
              child.isDisabled = false;
            });
          }
        });
      }
    },
    onGroupClick(node) {
      if (node.children !== undefined || node.children !== null) {
        this.model.forEach((element) => {
          if (element.children !== undefined && element.label == node.label) {
            element.children.forEach((child) => {
              child.isDisabled = true;
            });
          }
        });
      }
    },
    onSubmit() {
      event.preventDefault();

      if (this.edit == false) {
        axios
          .post("api/med-izzina", {
            sessionId: this.$store.state.sessionId,
            gprs_id: this.personData.id,
            gprs_pk: this.persKods,
            gprs_vards: this.personData.vards,
            gprs_uzvards: this.personData.uzvards,
            kaim_kods: "VMI_PIEV",
            sd: this.startDate.toISOString().split("T")[0],
            bd: this.endDate.toISOString().split("T")[0],
            kk: this.kategorijas,
            kier: this.ierobezojumi,
          })
          .then((response) => {
            if (response.data.Errors !== undefined) {
              toast.error(response.data.Errors[0].ClientMessage);
            } else {
              toast.success("Izziņa izveidota veiksmīgi.");
              this.$emit("doneForm");
            }
          })
          .catch((error) => console.log(error));
      } else {
        axios
          .post("api/med-izzina", {
            sessionId: this.$store.state.sessionId,
            gprs_id: this.personData.id,
            gprs_pk: this.persKods,
            gprs_vards: this.personData.vards,
            gprs_uzvards: this.personData.uzvards,
            id: this.izzinasData.id,
            kaim_kods: "VMI_LAB",
            sd: this.startDate.toISOString().split("T")[0],
            bd: this.endDate.toISOString().split("T")[0],
            kk: this.kategorijas,
            kier: this.ierobezojumi,
          })
          .then((response) => {
            if (response.data.Errors !== undefined) {
              toast.error(response.data.Errors[0].ClientMessage);
            } else {
              toast.success("Izziņa labota veiksmīgi.");
              this.$emit("doneForm");
            }
          })
          .catch((error) => console.log(error));
      }
    },
    setStartDate(event) {
      this.startDate = event;
    },
    setEndDate(event) {
      this.endDate = event;
    },
    dateToString() {
      //console.log(this.startDate.toISOString().split("T")[0]);
      //console.log(this.startDate.toISOString().split("T")[0]);

      var startMm = this.startDate.getMonth() + 1;
      var startDd = this.startDate.getDate();

      var startDateString = [
        (startDd > 9 ? "" : "0") + startDd,
        (startMm > 9 ? "" : "0") + startMm,
        this.startDate.getFullYear(),
      ].join(".");

      var endMm = this.endDate.getMonth() + 1;
      var endDd = this.endDate.getDate();

      var endDateString = [
        (startDd > 9 ? "" : "0") + endDd,
        (endMm > 9 ? "" : "0") + endMm,
        this.endDate.getFullYear(),
      ].join(".");

      /*console.log(
        "Start date: " + startDateString + "\n",
        "End date: " + endDateString
      );*/
    },
  },
  computed: {
    addYears() {
      this.dateChanged = true;
      if (this.yearsToAdd <= 20) {
        this.endDate = new Date(
          this.endDate.setFullYear(new Date().getFullYear() + this.yearsToAdd)
        );
      } else {
        this.endDate = new Date(
          this.endDate.setFullYear(new Date().getFullYear() + 20)
        );
      }
      this.dateToString();
    },
  },
  mounted() {
    if (this.izzinasData !== null && this.izzinasData !== undefined) {
      this.edit = true;
      this.startDate = new Date(this.izzinasData.sd);
      this.endDate = new Date(this.izzinasData.bd);
      this.kategorijas = this.izzinasData.kk;
      this.ierobezojumi = this.izzinasData.kier;
    }

    axios
      .post("/api/med-izzinas-atlaujas", {
        sessionId: this.$store.state.sessionId,
        pers_id: this.personData.id,
        jauns: 1,
      })
      .then((response) => {
        if (response.data.Errors !== undefined) {
          toast.error(response.data.Errors[0].ClientMessage);
        }
        this.atlautieKk = response.data["rowset-kk"];

        response.data["rowset-kier"].forEach((element) => {
          if (element.p_kods == null) {
            this.model.push({
              id: element.kods,
              label: element.kods + " - " + element.nos,
              children: [],
            });
          } else {
            parent = this.model.find((item) => item.id == element.p_kods);
            parent.children.push({
              id: element.kods,
              isDisabled: false,
              label: element.kods + " - " + element.nos,
            });
          }
        });

        this.model.forEach((element) => {
          if (element.children.length == 0) {
            delete element.children;
          }
        });
      })
      .catch((error) => console.log(error));
  },
};
</script>

<style scoped>
@import url("https://unpkg.com/vue-multiselect@2.1.0/dist/vue-multiselect.min.css");

.container {
  text-align: left;
}

.submit-button {
  margin-top: 20px;
}

.datums {
  margin-top: 10px;
}

.pogas {
  margin-top: 10px;
  margin-bottom: 10px;
}

.pogas .submit-button {
  width: 85px;
  margin-top: 5px;
}

.ierobezojumi {
  margin-top: 20px;
  margin-bottom: 10px;
}
</style>
